"use client";

import { useState, useEffect } from "react";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent } from "@/components/ui/card";
import { Textarea } from "@/components/ui/textarea";

interface AssistantPopupProps {
  isOpen: boolean;
  onClose: () => void;
}

interface ChatMessage {
  id: number;
  type: 'user' | 'assistant';
  content: string;
  timestamp: Date;
}

const quickActions = [
  { label: "Show inventory summary", command: "inventory_summary" },
  { label: "Generate sales report", command: "sales_report" },
  { label: "Check low stock items", command: "low_stock" },
  { label: "Payment status overview", command: "payment_status" },
  { label: "Create new invoice", command: "create_invoice" },
  { label: "Employee attendance", command: "attendance" }
];

const mockResponses = {
  inventory_summary: "üìä **Inventory Summary:**\n\n‚Ä¢ Total Products: 1,247\n‚Ä¢ In Stock: 1,019 (82%)\n‚Ä¢ Low Stock: 23 items need attention\n‚Ä¢ Out of Stock: 5 items require immediate restocking\n‚Ä¢ Total Inventory Value: ‚Çπ28,47,392\n\n**Top Categories:**\n‚Ä¢ Electronics: 45% of inventory\n‚Ä¢ Accessories: 28% of inventory\n‚Ä¢ Computers: 27% of inventory",
  
  sales_report: "üìà **Sales Report (Today):**\n\n‚Ä¢ Total Sales: ‚Çπ45,670\n‚Ä¢ Orders Processed: 156\n‚Ä¢ Pending Orders: 12\n‚Ä¢ Average Order Value: ‚Çπ293\n\n**Platform Breakdown:**\n‚Ä¢ Direct Sales: ‚Çπ18,450 (40%)\n‚Ä¢ Amazon: ‚Çπ15,230 (33%)\n‚Ä¢ Flipkart: ‚Çπ8,990 (20%)\n‚Ä¢ Meesho: ‚Çπ3,000 (7%)\n\n**Trend:** +12% compared to yesterday",
  
  low_stock: "‚ö†Ô∏è **Low Stock Alert:**\n\n**Critical (Out of Stock):**\n‚Ä¢ MacBook Air M3 - 0 units\n‚Ä¢ iPhone 14 Pro - 0 units\n\n**Low Stock (Below Minimum):**\n‚Ä¢ Samsung Galaxy S24 - 5 units (Min: 10)\n‚Ä¢ AirPods Pro 2 - 8 units (Min: 20)\n‚Ä¢ Dell XPS 13 - 3 units (Min: 8)\n\n**Recommendation:** Trigger auto-reorder for these items immediately.",
  
  payment_status: "üí∞ **Payment Status Overview:**\n\n‚Ä¢ Total Outstanding: ‚Çπ89,450\n‚Ä¢ Overdue Payments: ‚Çπ23,400 (3 invoices)\n‚Ä¢ Due This Week: ‚Çπ45,200 (8 invoices)\n‚Ä¢ Collection Rate: 85%\n\n**Action Required:**\n‚Ä¢ Send reminders for 3 overdue payments\n‚Ä¢ Follow up on ‚Çπ23,400 overdue amount\n‚Ä¢ Payment efficiency is good at 85%",
  
  create_invoice: "üìÑ **Invoice Creation Guide:**\n\n1. Go to Invoice Generator\n2. Select platform (Amazon/Flipkart/Meesho/Direct)\n3. Add customer details\n4. Add products with quantities\n5. System will auto-calculate GST\n6. Generate QR code and barcode\n7. Download PDF or send via email\n\n**Tip:** Use platform-specific templates for marketplace orders.",
  
  attendance: "üë• **Employee Attendance Summary:**\n\n‚Ä¢ Total Employees: 24\n‚Ä¢ Present Today: 22 (92%)\n‚Ä¢ On Leave: 2 employees\n‚Ä¢ Average Monthly Attendance: 88%\n\n**Department Wise:**\n‚Ä¢ Sales: 95% attendance\n‚Ä¢ Operations: 90% attendance\n‚Ä¢ Finance: 85% attendance\n‚Ä¢ Marketing: 92% attendance"
};

export default function AssistantPopup({ isOpen, onClose }: AssistantPopupProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([
    {
      id: 1,
      type: 'assistant',
      content: "Hello! I'm your AI business assistant. I can help you with inventory management, sales analysis, payment tracking, and more. How can I assist you today?",
      timestamp: new Date()
    }
  ]);
  const [inputMessage, setInputMessage] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const [isListening, setIsListening] = useState(false);

  const handleSendMessage = async (message: string) => {
    if (!message.trim()) return;

    // Add user message
    const userMessage: ChatMessage = {
      id: Date.now(),
      type: 'user',
      content: message,
      timestamp: new Date()
    };
    setMessages(prev => [...prev, userMessage]);
    setInputMessage("");
    setIsTyping(true);

    // Simulate AI processing delay
    setTimeout(() => {
      const response = generateAIResponse(message);
      const assistantMessage: ChatMessage = {
        id: Date.now() + 1,
        type: 'assistant',
        content: response,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, assistantMessage]);
      setIsTyping(false);
    }, 1500);
  };

  const generateAIResponse = (userMessage: string): string => {
    const message = userMessage.toLowerCase();
    
    // Check for specific commands
    for (const [key, response] of Object.entries(mockResponses)) {
      if (message.includes(key.replace('_', ' ')) || 
          message.includes(key) ||
          (key === 'inventory_summary' && (message.includes('inventory') || message.includes('stock summary'))) ||
          (key === 'sales_report' && (message.includes('sales') || message.includes('revenue'))) ||
          (key === 'low_stock' && (message.includes('low stock') || message.includes('out of stock'))) ||
          (key === 'payment_status' && (message.includes('payment') || message.includes('outstanding'))) ||
          (key === 'create_invoice' && (message.includes('invoice') || message.includes('bill'))) ||
          (key === 'attendance' && (message.includes('attendance') || message.includes('employee')))) {
        return response;
      }
    }

    // General responses
    if (message.includes('help') || message.includes('what can you do')) {
      return "ü§ñ **I can help you with:**\n\n‚Ä¢ **Inventory Management** - Stock levels, alerts, reordering\n‚Ä¢ **Sales Analysis** - Revenue reports, trends, platform performance\n‚Ä¢ **Payment Tracking** - Outstanding amounts, collection rates\n‚Ä¢ **Invoice Generation** - Create bills for different platforms\n‚Ä¢ **Employee Management** - Attendance, payroll, performance\n‚Ä¢ **Business Insights** - Analytics, recommendations, alerts\n\nJust ask me anything about your business operations!";
    }

    if (message.includes('thank') || message.includes('thanks')) {
      return "You're welcome! I'm here to help you manage your business more efficiently. Is there anything else you'd like to know?";
    }

    // Default response with suggestions
    return `I understand you're asking about "${userMessage}". Here are some things I can help you with:\n\n‚Ä¢ Check inventory status and stock levels\n‚Ä¢ Generate sales and payment reports\n‚Ä¢ Create invoices for different platforms\n‚Ä¢ Monitor employee attendance\n‚Ä¢ Provide business insights and recommendations\n\nTry asking something like "Show me inventory summary" or "Check payment status".`;
  };

  const handleQuickAction = (command: string) => {
    const action = quickActions.find(a => a.command === command);
    if (action) {
      handleSendMessage(action.label);
    }
  };

  const startVoiceRecognition = () => {
    if (!('webkitSpeechRecognition' in window) && !('SpeechRecognition' in window)) {
      alert("Voice recognition is not supported in your browser. Please use Chrome or Edge.");
      return;
    }

    const SpeechRecognition = (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;
    const recognition = new SpeechRecognition();
    
    recognition.continuous = false;
    recognition.interimResults = false;
    recognition.lang = 'en-US';

    setIsListening(true);

    recognition.onresult = (event: any) => {
      const transcript = event.results[0][0].transcript;
      setInputMessage(transcript);
      setIsListening(false);
    };

    recognition.onerror = () => {
      setIsListening(false);
      alert("Voice recognition error. Please try again.");
    };

    recognition.onend = () => {
      setIsListening(false);
    };

    recognition.start();
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[80vh] flex flex-col">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            ü§ñ AI Business Assistant
            <Badge variant="default">Online</Badge>
          </DialogTitle>
          <DialogDescription>
            Get instant insights about your business operations, inventory, sales, and more
          </DialogDescription>
        </DialogHeader>

        <div className="flex-1 flex gap-4 min-h-0">
          {/* Chat Area */}
          <div className="flex-1 flex flex-col min-h-0">
            {/* Messages */}
            <div className="flex-1 overflow-y-auto space-y-4 p-4 border rounded-lg bg-gray-50 min-h-0">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div
                    className={`max-w-[80%] p-3 rounded-lg ${
                      message.type === 'user'
                        ? 'bg-blue-600 text-white'
                        : 'bg-white border shadow-sm'
                    }`}
                  >
                    <div className="whitespace-pre-wrap text-sm">
                      {message.content}
                    </div>
                    <div className={`text-xs mt-1 ${
                      message.type === 'user' ? 'text-blue-100' : 'text-gray-500'
                    }`}>
                      {message.timestamp.toLocaleTimeString()}
                    </div>
                  </div>
                </div>
              ))}
              
              {isTyping && (
                <div className="flex justify-start">
                  <div className="bg-white border shadow-sm p-3 rounded-lg">
                    <div className="flex space-x-1">
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                    </div>
                  </div>
                </div>
              )}
            </div>

            {/* Input Area */}
            <div className="mt-4 space-y-3">
              <div className="flex gap-2">
                <Input
                  value={inputMessage}
                  onChange={(e) => setInputMessage(e.target.value)}
                  placeholder="Ask me anything about your business..."
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !e.shiftKey) {
                      e.preventDefault();
                      handleSendMessage(inputMessage);
                    }
                  }}
                  className="flex-1"
                />
                <Button
                  onClick={startVoiceRecognition}
                  variant="outline"
                  disabled={isListening}
                  className={isListening ? "animate-pulse" : ""}
                >
                  {isListening ? "Listening..." : "üé§"}
                </Button>
                <Button onClick={() => handleSendMessage(inputMessage)}>
                  Send
                </Button>
              </div>
              
              {isListening && (
                <div className="text-center text-sm text-blue-600">
                  üé§ Listening... Speak now
                </div>
              )}
            </div>
          </div>

          {/* Quick Actions Sidebar */}
          <div className="w-80 space-y-4">
            <Card>
              <CardContent className="p-4">
                <h3 className="font-semibold mb-3">Quick Actions</h3>
                <div className="space-y-2">
                  {quickActions.map((action) => (
                    <Button
                      key={action.command}
                      variant="outline"
                      size="sm"
                      className="w-full justify-start text-left h-auto py-2"
                      onClick={() => handleQuickAction(action.command)}
                    >
                      {action.label}
                    </Button>
                  ))}
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <h3 className="font-semibold mb-3">Voice Commands</h3>
                <div className="space-y-2 text-sm text-gray-600">
                  <p>‚Ä¢ "Show inventory summary"</p>
                  <p>‚Ä¢ "Check payment status"</p>
                  <p>‚Ä¢ "Generate sales report"</p>
                  <p>‚Ä¢ "What's my attendance rate?"</p>
                  <p>‚Ä¢ "Help me create an invoice"</p>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <h3 className="font-semibold mb-3">AI Capabilities</h3>
                <div className="space-y-2 text-sm text-gray-600">
                  <p>‚úÖ Business Analytics</p>
                  <p>‚úÖ Inventory Insights</p>
                  <p>‚úÖ Sales Forecasting</p>
                  <p>‚úÖ Payment Tracking</p>
                  <p>‚úÖ Employee Management</p>
                  <p>‚úÖ Voice Recognition</p>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>

        <div className="flex justify-between items-center pt-4 border-t">
          <div className="text-xs text-gray-500">
            Powered by AI ‚Ä¢ Real-time business insights
          </div>
          <Button variant="outline" onClick={onClose}>
            Close Assistant
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
